/* src/components/MovieList.css */

.movie-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  gap: 20px;
  margin-top: 20px;
  padding: 0 20px;
}

.movie-card {
  background-color: #1a1a1a;
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.2s ease-in-out;
  color: #fff;
  text-align: center;
}

.movie-card:hover {
  transform: scale(1.05);
}

.movie-card img {
  width: 100%;
  height: auto;
  border-bottom: 2px solid #ffcc00;
}

.movie-card h3 {
  font-size: 16px;
  margin: 10px;
  color: #ffcc00;
}
