/* General Layout */
body {
  background-color: #5a5959;
  color: #fff;
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.App {
  text-align: center;
  padding-bottom: 100px; /* Ensures content doesn't get hidden behind the fixed footer */
}

/* Header */
.header {
  padding: 20px;
  background-color: #fd6666;
  font-size: 24px;
  font-weight: bold;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-bottom: 1px solid #222;
}

.header span {
  font-size: 32px;
  margin-bottom: 5px;
}

/* Section Titles */
.section-title,
.page-title {
  font-size: 28px;
  margin: 30px 0 20px;
  color: #ff3d3d;
  text-align: center;
  letter-spacing: 1px;
  font-weight: 600;
}

/* Genre Buttons */
.genre-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 20px;
  gap: 10px;
  padding: 0 20px;
}

.genre-button {
  background-color: #333;
  color: #fff;
  border: none;
  padding: 10px 18px;
  border-radius: 20px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
}

.genre-button:hover {
  background-color: #ff3d3d;
}

/* Genre Select Dropdown */
.genre-select {
  padding: 10px 14px;
  margin: 10px;
  border-radius: 6px;
  background-color: #222;
  color: #fff;
  border: 1px solid #444;
  font-size: 14px;
}

/* Movie List Grid */
.movie-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  padding: 0 20px 80px;
}

.movie-card {
  background-color: #1e1e1e;
  padding: 10px;
  border-radius: 10px;
  width: 150px;
  transition: transform 0.3s;
  box-shadow: 0 4px 12px rgba(255, 255, 255, 0.05);
}

.movie-card:hover {
  transform: scale(1.05);
}

.movie-card img {
  width: 100%;
  border-radius: 10px;
}

.movie-card-title,
.movie-card h3 {
  font-size: 14px;
  margin-top: 8px;
  color: #ccc;
  text-align: center;
}

/* Footer */
.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: #f15959;
  border-top: 1px solid #444;
  z-index: 999;
}

.footer-nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  padding: 10px 0;
  font-size: 14px;
}

.footer-link {
  text-decoration: none;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 500;
  gap: 4px; /* Fixes spacing between icon and text */
}

.footer-link:hover {
  color: #fc8c8c;
}

.footer-text,
.credit {
  text-align: center;
  padding-bottom: 6px;
  font-size: 12px;
  color: #f8f1f1;
  margin-top: 4px;
}
